:root {
  --rv-space-gap: 8px;
  --rv-space-gap-vertical: 8px;
  --rv-space-gap-horizontal: 8px;
}
.rv-space__item {
  flex: none;
}
.rv-space {
  display: inline-flex;
  --gap: var(--rv-space-gap);
  --gap-vertical: var(--gap);
  --gap-horizontal: var(--gap);
}
.rv-space--vertical {
  flex-direction: column;
}
.rv-space--vertical > .rv-space__item {
  margin-bottom: var(--gap-vertical);
}
.rv-space--vertical > .rv-space__item:last-child {
  margin-bottom: 0;
}
.rv-space--horizontal {
  flex-direction: row;
}
.rv-space--horizontal > .rv-space__item {
  margin-right: var(--gap-horizontal);
}
.rv-space--horizontal > .rv-space__item:last-child {
  margin-right: 0;
}
.rv-space--horizontal.rv-space--wrap {
  flex-wrap: wrap;
  margin-bottom: calc(var(--gap-vertical) * -1);
}
.rv-space--horizontal.rv-space--wrap > .rv-space__item {
  padding-bottom: var(--gap-vertical);
}
.rv-space.rv-space--block {
  display: flex;
}
.rv-space--align-center {
  align-items: center;
}
.rv-space--align-start {
  align-items: flex-start;
}
.rv-space--align-end {
  align-items: flex-end;
}
.rv-space--align-baseline {
  align-items: baseline;
}
.rv-space--justify-center {
  justify-content: center;
}
.rv-space--justify-start {
  justify-content: flex-start;
}
.rv-space--justify-end {
  justify-content: flex-end;
}
.rv-space--justify-between {
  justify-content: space-between;
}
.rv-space--justify-around {
  justify-content: space-around;
}
.rv-space--justify-evenly {
  justify-content: space-evenly;
}
.rv-space--justify-stretch {
  justify-content: stretch;
}
