:root {
  --rv-typography-color: var(--rv-text-color);
  --rv-typography-link-color: var(--rv-primary-color);
  --rv-typography-font-size: var(--rv-font-size-md);
  --rv-typography-line-height: var(--rv-line-height-md);
  --rv-typography-primary-color: var(--rv-primary-color);
  --rv-typography-danger-color: var(--rv-danger-color);
  --rv-typography-success-color: var(--rv-success-color);
  --rv-typography-secondary-color: var(--rv-gray-6);
  --rv-typography-warning-color: var(--rv-warning-color);
  --rv-typography-disabled-color: var(--rv-gray-5);
  --rv-typography-light-color: var(--rv-white);
}
.rv-typography {
  color: var(--rv-typography-color);
  word-wrap: break-word;
}
.rv-typography--xs {
  font-size: calc(0.8 * var(--rv-typography-font-size));
  line-height: calc(0.8 * var(--rv-typography-line-height));
}
.rv-typography--sm {
  font-size: calc(0.9 * var(--rv-typography-font-size));
  line-height: calc(0.9 * var(--rv-typography-line-height));
}
.rv-typography--md,
.rv-typography--5 {
  font-size: var(--rv-typography-font-size);
  line-height: var(--rv-typography-line-height);
}
.rv-typography--lg,
.rv-typography--l4 {
  font-size: calc(1.2 * var(--rv-typography-font-size));
  line-height: calc(1.2 * var(--rv-typography-line-height));
}
.rv-typography--xl,
.rv-typography--l3 {
  font-size: calc(1.4 * var(--rv-typography-font-size));
  line-height: calc(1.4 * var(--rv-typography-line-height));
}
.rv-typography--xxl,
.rv-typography--l2 {
  font-size: calc(1.6 * var(--rv-typography-font-size));
  line-height: calc(1.6 * var(--rv-typography-line-height));
}
.rv-typography--primary {
  color: var(--rv-typography-primary-color);
}
.rv-typography--danger {
  color: var(--rv-typography-danger-color);
}
.rv-typography--success {
  color: var(--rv-typography-success-color);
}
.rv-typography--secondary {
  color: var(--rv-typography-secondary-color);
}
.rv-typography--light {
  color: var(--rv-typography-light-color);
}
.rv-typography--warning {
  color: var(--rv-typography-warning-color);
}
.rv-typography--disabled {
  color: var(--rv-typography-disabled-color);
  cursor: not-allowed;
}
.rv-typography--underline {
  text-decoration: underline;
}
.rv-typography--delete {
  text-decoration: line-through;
}
.rv-typography--text {
  display: inline-block;
}
.rv-typography--text.rv-ellipsis {
  display: block;
}
.rv-typography--title {
  font-weight: bold;
}
.rv-typography--l1 {
  margin-bottom: 25px;
  font-size: calc(2 * var(--rv-typography-font-size));
  line-height: calc(2 * var(--rv-typography-line-height));
}
.rv-typography--l2 {
  margin-bottom: 20px;
}
.rv-typography--l3 {
  margin-bottom: 15px;
}
.rv-typography--l4 {
  margin-bottom: 10px;
}
.rv-typography--l5 {
  margin-bottom: 6px;
}
.rv-typography--link {
  color: var(--rv-typography-link-color);
  cursor: pointer;
}
.rv-typography--center {
  text-align: center;
}
.rv-typography--strong {
  font-weight: bold;
}
